<template>
  <div id="login">
    <div class="container-fluid">
      <Header/>
      <hr style="border: none">
      <div class="login">
        <div class="col-6 pl-0"><img src="../../assets/img/ly_reg.png" height="560"></div>
        <div class="col-6 text-center pr-7" >
          <div class="main-title">登录</div>
          <div class="second-title">砺英网 | 通行证</div>
          <div class="row mt-6">
            <div class="center-block" style="width:300px;">
              <div class="row text-center">
                <el-form  class="mt-3" ref="form" :model="form" :rules="rules"  :show-message="true">
                  <el-form-item prop="phone">
                    <el-input placeholder="你的手机号" v-model="form.phone" maxlength="11"></el-input>
                  </el-form-item>
                  <el-form-item  prop="pwd">
                    <el-input placeholder="输入密码" v-model="form.pwd" show-password maxlength="20" @keyup.enter.native="login('form')"></el-input>
                  </el-form-item>
                </el-form>
              </div>
              <div class="row mt-6 mb-3">
                <div class="btn btn-block login-btn" @click="login('form')" >登录</div>
              </div>
              <div class="row">
                <a class="pull-left reg-pwd" href="reg-student.html">注册新账号</a>
                <a class="pull-right reg-pwd" style="color: #999999" href="forget-pwd.html">忘记密码？</a>
              </div>
              <div class="row mt-6">
                <div class="col-4"><hr class="mb-0 mt-4"></div>
                <div class="col-4"><i class="iconfont iconbangdingweixin" style="color: #32C25A"></i><span class="wechat-login">微信登录</span></div>
                <div class="col-4"><hr class="mb-0 mt-4"></div>
              </div>
            </div>
          </div>
        </div>
      </div>
      <Footer/>
    </div>
  </div>
</template>

<script>
import Header from '../../components/reg/Header.vue'
import Footer from '../../components/reg/Footer.vue'
export default {
  name: 'login',
  components: {
    Header,
    Footer
  },
  mounted () {
    localStorage.clear()
  },
  data () {
    var validatePhone = (rule, value, callback) => {
      if (value === '') {
        callback(new Error('请输入手机号'))
      } else {
        if (this.form.phone !== '') {
          var reg = /^1([38]\d|5[0-35-9]|7[3678])\d{8}$/
          if (!reg.test(this.form.phone)) {
            callback(new Error('手机号格式不正确'))
          }
        }
        callback()
      }
    }
    var filterPhone = (rule, value, callback) => {
      this.form.phone = value.replace(/[^\d]/g, '')
      callback()
    }
    var filterPass = (rule, value, callback) => {
      if (value === '') {
        callback(new Error('请输入密码 '))
      }
      var patrn = /^(\w){6,20}$/
      if (!patrn.exec(value)) {
        callback(new Error('只能输入6-20个字母、数字、下划线 '))
      }
      callback()
    }
    return {
      form: {
        phone: '',
        pwd: ''
      },
      rules: {
        phone: [
          { min: 11, max: 11, message: '手机号长度应为11位', trigger: 'blur' },
          { validator: validatePhone, trigger: 'blur' },
          { validator: filterPhone, trigger: 'change' }
          // { required: true, message: '请输入手机号码', trigger: 'blur' },
          // { min: 11, max: 11, message: '手机号不完整', trigger: 'blur' },
          // { pattern: /^1([38]\d|5[0-35-9]|7[3678])\d{8}$/, message: '手机号格式不正确', trigger: 'blur' }
        ],
        pwd: [
          { validator: filterPass, trigger: 'change' },
          { validator: filterPass, trigger: 'blur' }
        ]
      }
    }
  },
  methods: {
    login (formName) {
      let me = this

      // eslint-disable-next-line no-undef
      // this.showPDF('/media/up/2018/3/9/494079c7ec333bd371798dd0a73c0a0b.pdf')
      me.$refs[formName].validate((valid) => {
        if (valid) {
          this.$http.post('/user/login', {
            'phone': this.form.phone,
            'password': this.form.pwd
          }, res => {
            if (res.success) {
              localStorage.setItem('token', res.data.token)
              localStorage.setItem('userType', res.data.userType)
              if (res.data.userType === '0') {
                window.location = 'personal-main.html'
              } else {
                window.location = 'assistant-main.html'
              }
            } else {
              me.$alert(res.errorMsg, '提示', {
                confirmButtonText: '确定'
              })
            }
          })
        } else {
          return false
        }
      })
      // window.location = 'personal-main.html'
    }
  }
}
</script>

